@import url("https://fonts.googleapis.com/css2?family=Vollkorn:ital,wght@0,400;0,600;1,600&display=swap");

.human-biases
{
    --shadow: #000;
    --background: #fff;
    --foreground: #000;
    --links: #000;
    --col-hover: orange;
    --col-links: #4d6b2e;
    --opacity: 0.6;
    --padding: 12px;
    --size: 0;
    --rotation: 0;
    --oversize: 1024px;
    position: relative;
    display:block;
    // dont use legibility it chokes
  text-rendering: optimizeSpeed;
  color: var(--foreground);
  background-color: var(--background);
 
}

:host{
    background-color: var(--background);
}

:host:focus-within {
    // prefer smooth scrolling where available (opinionated).
    scroll-behavior: smooth;
}

#infographic{
    position: relative;
    display: grid;
    // flex-direction: column;
    // depends?
    // overflow: auto;
}

#learn-more{

}

a{
    color:var(--links, red);
}
a:focus,
a:hover,
button:focus,
button:hover {
  color: var(--col-hover);
}
button,
select {
  font: inherit;
  cursor: pointer;
  appearance: none;
  user-select: none;
  color: var(--background);
  border: 2px solid var(--background);
  border-radius: 2px;
  background: var(--foreground);
  padding: 4px;
}


#errors{
    display: none;
    position:fixed;
    top:var(--padding);
    left:var(--padding);
    right:var(--padding);
    height:calc(100vh - 2 * var(--padding));
    background:red;
    z-index:1;
    font-size:200%;
}

#errors.visible{
    display: block;
}
header,
header > * {
  box-sizing: border-box;
}

header{
    // position: absolute;
    // top: 0;
    // left: 1rem;
    // right: 1rem;
    //max-width: 100vw;
  //  min-height: 100vh;
    width:100%;
    // pointer-events: none;
    // overflow: hidden;
    grid-area: 1/1/1/1;
    // this break with the shape
    // display: flex;
    // flex-direction: column;
    // justify-content: center;
}

// circular spacer
.human-biases header::before{
    content: "";
    display: block;
    border-radius: 99999px;
    shape-outside: circle(46%);
    pointer-events: none;
    // width: 100%;
    box-sizing: border-box;
    float: right;
    float: left;
    /*  
    height:0;
   width:100%;
    padding-bottom:100%;
    height:calc(100% - 12%);
    border-radius: 99999px;
    shape-outside: circle(50%);
    background-color:rgba(0,0,0,0.5) ;
    margin:6% auto;
    
    width: 100%;
    max-width: calc(50% + 270px);*/
    --w: calc(100vmin + var(--oversize) * var(--size) - 4rem);
   margin-left: 10%;//4vw;//calc( (100vw - var(--w)) * 0.5 );
    // padding-left: calc( var(--w));
    width: var(--w);
    height: var(--w);
}

section:empty,
div:empty{
    display: none;
}

// the read more button under the headline
#read-more{
    // just an icon in a circle
    width:10vmin;
    height:10vmin;
    border-radius: 99%;
    display:block;
    margin:auto;
}

.wallpaper{
    fill:transparent;
    border-radius: 50%;
}

svg.biases{
    --rotation:0;/* Handled via JS now ? 283; */
    --oversize:1024px;
    grid-area: 1/1/1/1;
    height: calc(100vmin + var(--oversize) * var(--size,1) - 4rem);
    align-items: center;
    display: flex;
   // margin: 0 auto;
    box-sizing: border-box;
    order: -1;
    width: calc(100vmin + var(--oversize) * var(--size, 1));
    background-color: var(--background);
    // clip rotated text
    overflow:hidden;
}

.visualiser{
    /* slow slow slow so try to speed up */
    will-change: transform;
    transition:transform 45ms ease-out;
    transform-origin: center center;
    /* transform:rotate( calc(1deg * var(--rotation) ) ) translate(50%, 50%) translateZ(0); */
}

/* texts */
text {
    font-size: 6px;
    transition:fill 400ms ease-out;
    user-select:none;
    text-shadow: 0 1px 0 var(--shadow), 0 -1px 0 var(--shadow), 1px 0 0 var(--shadow), -1px 0 0 var(--shadow);
}

h1,h2,h3,h4,h5,h6,
.heading, .subheading, .title{
    font-family: 'Vollkorn', 'Noto Serif', serif;
    font-weight: 600;
    pointer-events: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 3vw;
  font-size: clamp(
    var(--fluid-type-min, 1rem),
    calc(1rem + var(--fluid-type-target, 3vw)),
    var(--fluid-type-max, 1.3rem)
  );
  text-shadow: 0 1px 0 var(--shadow), 0 -1px 0 var(--shadow),
    1px 0 0 var(--shadow), -1px 0 0 var(--shadow);
}
h2,
h3 {
//   max-width: 30ch;
}
p {
//   max-width: 60ch;
}

.heading,
h1 {
  --fluid-type-min: 1.3rem;
  --fluid-type-max: 1.4rem;
  --fluid-type-target: 2.3vw;
//   max-width: 15ch;
  text-transform: capitalize;
  opacity: 0.8;
  line-height: 1.3;
}
.subheading,
h2 {
  margin-top: 0;
  --fluid-type-min: 1rem;
  --fluid-type-max: 1.3rem;
  font-style: italic;
  opacity: 0.9;
  line-height: 1.2;
//   text-align: center;
}
.title {
  font-size: 2rem;
  text-transform: capitalize;
  fill: var(--foreground);
  background: var(--color);
  opacity: 0.9;
}
.title,
.trait {
  letter-spacing: 0.1em;
}
.trait {
  font-size: 7px;
}
.description{
    max-width: 20ch;
    font-weight: 700;
    width:5em;
}
.legend{
    font-size: 12px;
    font-weight: 700;
}

.description-ring{
    /*stroke: #03163C;*/
    stroke-opacity: var(--opacity);
    stroke-width: 4px;
    stroke:var(--foreground);
}

.node{
    cursor: pointer;
}

/* circles */
.node .blob {
    /*fill: #999;*/
    opacity:var(--opacity);
    transition: opacity 400ms ease-out, 
                fill 400ms ease-out;
}

/* The lines that join one node to the next */
.node .connection {
    fill: none;
    /*stroke: #03163C;*/
    stroke-opacity:var(--opacity);
    stroke-width: 1px;
    transition: stroke 200ms ease-out, 
                stroke-opacity 90ms ease-out,
                stroke-width 40ms ease-out;
}

/* Outer rim blobs */
.description-blob{
    stroke:var(--background);
    transition: transform 200ms ease-out, 
                stroke 200ms ease-out;
}

/* This is the outer text fill:var(--foreground); */
.description text{
    
    /* transform-origin: 50% 50%; */
    /* transform:translate(-50%,-50%) rotate(calc(-1deg * var(--rotation) )) ; */
}


/* Use CSS text - set via component */
.human-biases.text-css svg > text,
.human-biases.text-svg header,
:host.text-css svg > text,
:host.text-svg header {
  display: none;
}

/* The very centre dot! */
.blob.circle-0{
    /* r:8;
    fill:pink; */
    pointer-events: none;
    opacity:1;
}

// WIKI data below
.information{
    background-color: var(--background);
    padding:clamp(30px, 4vw, 40px);
}



