/* UI */
button{
    cursor:pointer;
    user-select:none;
    -webkit-appearance: none;
    color:var(--background);
    border:2px solid var(--background);
    border-radius:2px;
    background:var(--foreground);
}


.controls {
    order: -1;
    text-align: center;
    margin:auto;
    padding:4px 6px;
    // position: sticky;
    // bottom: 0;

     // display: flex;
    // flex-direction: column;

    // justify-content: center;
    --min: 10ch;
    
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    

    // stick to bottom if possible
    position: sticky;
    bottom: 0;
    left:0;
    right:0;
      
     > * {
        flex: 1 1 var(--min);
    }
}

fieldset{
